$sec-color: #fafafa;
$sec-color-shade-1: #f0f0f0;
$navbar-h: 90px;

body {
  background-color: $sec-color;
}

.navbar {
  a {
    text-decoration: none;
  }

  .navicons {
    color: #0d6efd;
    font-size: 1.3rem;
  }
}

#container {
  display: flex;
  height: calc(100vh - $navbar-h);
  overflow: hidden;

  .sidebar {
    height: calc(100vh - $navbar-h);
    padding: 2em;

    $color : #b4b4b4;

    .menu-title {
      color: $color;
      text-transform: uppercase;
      font-size: .8em;
      margin-bottom: 1.3em;
      text-align: center;
    }

    .icons-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 1em;

      .icons {
        color: $color;
        border-radius: 9999px;
        padding: 19px;
        font-size: 14px;
        transition: all .3s;
        cursor: pointer;
      }

      .icons:hover {
        color: black;
        background-color: $sec-color-shade-1;
      }
    }

  }

  #content {
    background-color: white;
    min-height: calc(100vh - $navbar-h);
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 2em;
    border-radius: 1em;
  }
}
